import { OSMAdminCollection } from '../types';

/**
 * Fetch administrative boundary for address component using Enterprise BoundaryService
 * @param addressComponent - Component ŒºŒµ label Œ∫Œ±Œπ type
 * @returns OSM-style admin boundary collection
 */
export const fetchBoundaryByAddressComponent = async (
  addressComponent: { label: string; type: string }
): Promise<OSMAdminCollection> => {
  try {
    console.log(`üåç Fetching boundary Œ≥ŒπŒ±: ${addressComponent.label}`);

    // ŒßœÅŒ∑œÉŒπŒºŒøœÄŒøŒπŒøœçŒºŒµ Nominatim Œ≥ŒπŒ± ŒΩŒ± œÄŒ¨œÅŒøœÖŒºŒµ œÑŒø Œ†ŒõŒóŒ°ŒïŒ£ POLYGON
    const searchUrl = `https://nominatim.openstreetmap.org/search?q=${encodeURIComponent(addressComponent.label)}&format=json&limit=1&polygon_geojson=1`;

    const searchResponse = await fetch(searchUrl, {
      headers: {
        'User-Agent': 'Layera-GeoAlert/1.0'
      }
    });

    if (!searchResponse.ok) {
      throw new Error(`Nominatim error: ${searchResponse.status}`);
    }

    const searchData = await searchResponse.json();

    if (searchData && searchData.length > 0) {
      const result = searchData[0];

      // Œ†œÅŒøœÑŒØŒºŒ∑œÉŒ∑: ŒßœÅŒÆœÉŒ∑ œÑŒøœÖ œÄœÅŒ±Œ≥ŒºŒ±œÑŒπŒ∫Œøœç polygon Œ±ŒΩ œÖœÄŒ¨œÅœáŒµŒπ
      if (result.geojson) {
        console.log(`‚úÖ Found FULL POLYGON Œ≥ŒπŒ± ${addressComponent.label} ŒºŒµ ${result.geojson.coordinates?.[0]?.length || 0} œÉŒ∑ŒºŒµŒØŒ±`);

        return {
          type: 'FeatureCollection',
          features: [{
            type: 'Feature',
            properties: {
              name: result.display_name || addressComponent.label,
              admin_level: '8',
              boundary: 'administrative',
              osm_id: result.osm_id || 0,
              osm_type: result.osm_type || 'node'
            },
            geometry: result.geojson
          }]
        };
      }
      // Fallback: ŒßœÅŒÆœÉŒ∑ bounding box Œ±ŒΩ Œ¥ŒµŒΩ œÖœÄŒ¨œÅœáŒµŒπ polygon
      else if (result.boundingbox) {
        const bbox = result.boundingbox;

        // boundingbox format: [south, north, west, east]
        const south = parseFloat(bbox[0]);
        const north = parseFloat(bbox[1]);
        const west = parseFloat(bbox[2]);
        const east = parseFloat(bbox[3]);

        console.log(`‚ö†Ô∏è Using bounding box Œ≥ŒπŒ± ${addressComponent.label} (no polygon available)`);

        return {
          type: 'FeatureCollection',
          features: [{
            type: 'Feature',
            properties: {
              name: result.display_name || addressComponent.label,
              admin_level: '8',
              boundary: 'administrative',
              osm_id: result.osm_id || 0,
              osm_type: result.osm_type || 'node'
            },
            geometry: {
              type: 'Polygon',
              coordinates: [[
                [west, north],
                [east, north],
                [east, south],
                [west, south],
                [west, north]
              ]]
            }
          }]
        };
      }
    }

    // Default Overpass query Œ≥ŒπŒ± Œ¨ŒªŒªŒµœÇ œÑŒøœÄŒøŒ∏ŒµœÉŒØŒµœÇ
    const query = `
      [out:json][timeout:10];
      (
        relation["boundary"="administrative"]["name"~"${addressComponent.label}",i];
      );
      out ids;
    `;

    const response = await fetch('https://overpass-api.de/api/interpreter', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: `data=${encodeURIComponent(query)}`
    });

    if (!response.ok) {
      throw new Error(`Overpass API error: ${response.status}`);
    }

    const data = await response.json();

    if (data.elements && data.elements.length > 0) {
      console.log(`‚úÖ OSM boundary IDs found Œ≥ŒπŒ±: ${addressComponent.label} (${data.elements.length} elements)`);

      // ŒïœÄŒπœÉœÑœÅŒ≠œÜŒøœÖŒºŒµ null geometry Œ≥ŒπŒ± œÑœéœÅŒ±
      // Œ§Œø œÄœÅŒ±Œ≥ŒºŒ±œÑŒπŒ∫œå boundary œáœÅŒµŒπŒ¨Œ∂ŒµœÑŒ±Œπ œÄŒπŒø œÉœçŒΩŒ∏ŒµœÑŒø query
      return {
        type: 'FeatureCollection',
        features: []
      };
    }

    console.log(`‚ö†Ô∏è No OSM boundary found Œ≥ŒπŒ±: ${addressComponent.label}`);
    return {
      type: 'FeatureCollection',
      features: []
    };

  } catch (error) {
    console.error(`üö´ OSM API error Œ≥ŒπŒ± ${addressComponent.label}:`, error);

    // Fallback to local boundary system
    console.log(`‚ö†Ô∏è Fallback to local boundary system Œ≥ŒπŒ±: ${addressComponent.label}`);
    return await fetchLocalBoundary(addressComponent);
  }
};

/**
 * Fallback boundary fetch using local system
 */
async function fetchLocalBoundary(
  addressComponent: { label: string; type: string }
): Promise<OSMAdminCollection> {
  // Basic query normalization
  const baseName = addressComponent.label
    .replace(/^ŒîŒÆŒºŒøœÇ\s+/, '')
    .replace(/^ŒîŒ∑ŒºŒøœÑŒπŒ∫ŒÆ\s+ŒïŒΩœåœÑŒ∑œÑŒ±\s+/, '')
    .replace(/^Œ†ŒµœÅŒπœÜŒ≠œÅŒµŒπŒ±\s+/, '')
    .replace(/^Œ†ŒµœÅŒπœÜŒµœÅŒµŒπŒ±Œ∫ŒÆ\s+ŒïŒΩœåœÑŒ∑œÑŒ±\s+/, '')
    .replace(/\s+-\s+.*$/, '')
    .replace(/\s+\(.+\)$/, '')
    .trim();

  console.log('‚ö†Ô∏è Fallback to local boundary system Œ≥ŒπŒ±:', baseName);

  // Use existing fallback system if available
  try {
    const { findFallbackBoundary } = await import('./fallbackBoundaries');
    const searchTerms = [addressComponent.label, baseName];
    const fallbackBoundary = findFallbackBoundary(searchTerms);

    if (fallbackBoundary) {
      console.log('‚úÖ Local fallback boundary found');
      return fallbackBoundary;
    }
  } catch (fallbackError) {
    console.warn('‚ö†Ô∏è Fallback system not available:', fallbackError);
  }

  // Ultimate fallback: empty collection
  console.error('üö´ No boundary data available Œ≥ŒπŒ±:', addressComponent.label);
  return {
    type: 'FeatureCollection',
    features: []
  };
}

