/**
 * Address parsing utilities œÄŒøœÖ œáœÅŒ∑œÉŒπŒºŒøœÄŒøŒπŒøœçŒΩ œÑŒø @layera/geocoding
 */

// Temporarily inline GeocodeResult to avoid build dependency issues
interface Coordinates {
  latitude: number;
  longitude: number;
}

interface Address {
  street?: string;
  houseNumber?: string;
  postalCode?: string;
  city?: string;
  region?: string;
  country?: string;
}

interface GeocodeResult {
  id: string;
  displayName: string;
  coordinates: Coordinates;
  accuracy: string;
  address: Address;
}
import type { AddressComponent } from '../types';

/**
 * Parse Œ≠ŒΩŒ± GeocodeResult œÉŒµ clickable AddressComponents
 * ŒßœÅŒ∑œÉŒπŒºŒøœÄŒøŒπŒµŒØ œÑŒ± parsed fields Œ±œÄœå œÑŒø geocoding Œ±ŒΩœÑŒØ ŒΩŒ± Œ∫Œ¨ŒΩŒµŒπ custom parsing
 */
export function parseGeocodeToComponents(result: GeocodeResult): AddressComponent[] {
  const components: AddressComponent[] = [];
  let index = 0;

  // Street & House Number
  if (result.address.street && result.address.houseNumber) {
    components.push({
      id: `street-${index++}`,
      label: `${result.address.street} ${result.address.houseNumber}`,
      type: 'street',
      clickable: false, // Streets Œ¥ŒµŒΩ Œ≠œáŒøœÖŒΩ boundaries
      value: `${result.address.street} ${result.address.houseNumber}`,
      className: 'address-street'
    });
  } else if (result.address.street) {
    components.push({
      id: `street-${index++}`,
      label: result.address.street,
      type: 'street',
      clickable: false,
      value: result.address.street,
      className: 'address-street'
    });
  }

  // Postal Code
  if (result.address.postalCode) {
    components.push({
      id: `postal-${index++}`,
      label: result.address.postalCode,
      type: 'postalCode',
      clickable: false, // Postal codes Œ¥ŒµŒΩ Œ≠œáŒøœÖŒΩ boundaries
      value: result.address.postalCode,
      className: 'address-postal'
    });
  }

  // City (clickable Œ≥ŒπŒ± boundary)
  if (result.address.city) {
    components.push({
      id: `city-${index++}`,
      label: result.address.city,
      type: 'city',
      clickable: true, // Cities Œ≠œáŒøœÖŒΩ administrative boundaries
      value: result.address.city,
      className: 'address-city'
    });
  }

  // Region/ŒîŒÆŒºŒøœÇ (clickable Œ≥ŒπŒ± boundary)
  if (result.address.region) {
    components.push({
      id: `region-${index++}`,
      label: result.address.region,
      type: 'region',
      clickable: true, // Regions Œ≠œáŒøœÖŒΩ administrative boundaries
      value: result.address.region,
      className: 'address-region'
    });
  }

  // Country
  if (result.address.country) {
    components.push({
      id: `country-${index++}`,
      label: result.address.country,
      type: 'country',
      clickable: true, // Countries Œ≠œáŒøœÖŒΩ boundaries
      value: result.address.country,
      className: 'address-country'
    });
  }

  return components;
}

/**
 * Parse œÑŒø displayName œÉŒµ ŒµœÄŒπœÄŒªŒ≠ŒøŒΩ components œÄŒøœÖ Œ¥ŒµŒΩ œÖœÄŒ¨œÅœáŒøœÖŒΩ œÉœÑŒø parsed address
 * Useful Œ≥ŒπŒ± œÄŒµœÅŒπœÄœÑœéœÉŒµŒπœÇ œåœÄœâœÇ "ŒúŒ∑œÑœÅŒøœÄŒøŒªŒπœÑŒπŒ∫ŒÆ ŒïŒΩœåœÑŒ∑œÑŒ± ŒòŒµœÉœÉŒ±ŒªŒøŒΩŒØŒ∫Œ∑œÇ" œÄŒøœÖ Œ¥ŒµŒΩ ŒµŒØŒΩŒ±Œπ œÉœÑŒø address object
 */
export function parseDisplayNameToAdditionalComponents(
  result: GeocodeResult,
  existingComponents: AddressComponent[]
): AddressComponent[] {
  const displayName = result.displayName;
  const additionalComponents: AddressComponent[] = [];

  // Œ§ŒºŒÆŒºŒ±œÑŒ± œÑŒøœÖ displayName œÄŒøœÖ Œ¥ŒµŒΩ œÖœÄŒ¨œÅœáŒøœÖŒΩ ŒÆŒ¥Œ∑ œÉœÑŒ± components
  const existingValues = new Set(existingComponents.map(c => c.value?.toLowerCase()));

  // Split by comma Œ∫Œ±Œπ Œ∫Œ±Œ∏Œ±œÅŒπœÉŒºœåœÇ
  const parts = displayName.split(',').map((part: string) => part.trim());

  let index = existingComponents.length;

  for (const part of parts) {
    // Skip Œ±ŒΩ œÖœÄŒ¨œÅœáŒµŒπ ŒÆŒ¥Œ∑
    if (existingValues.has(part.toLowerCase())) {
      continue;
    }

    // Skip Œ±ŒΩ ŒµŒØŒΩŒ±Œπ ŒºœåŒΩŒø Œ±œÅŒπŒ∏ŒºœåœÇ (house number)
    if (/^\d+$/.test(part)) {
      continue;
    }

    // Skip Œ±ŒΩ ŒµŒØŒΩŒ±Œπ ŒºœåŒΩŒø postal code
    if (/^\d{5}$/.test(part)) {
      continue;
    }

      // UNIVERSAL APPROACH: ŒöŒ¨ŒΩŒøœÖŒºŒµ clickable ŒüŒõŒë ŒµŒ∫œÑœåœÇ Œ±œÄœå:
    // - ŒëœÅŒπŒ∏ŒºŒøœçœÇ ŒºœåŒΩŒø (house numbers)
    // - Postal codes
    // - Œ†ŒøŒªœç ŒºŒπŒ∫œÅŒ¨ strings (<=2 œáŒ±œÅŒ±Œ∫œÑŒÆœÅŒµœÇ)
    // Œ§Œø œÉœçœÉœÑŒ∑ŒºŒ± Œ∏Œ± ŒµŒªŒ≠Œ≥ŒæŒµŒπ ŒîŒ•ŒùŒëŒúŒôŒöŒë Œ±ŒΩ œÖœÄŒ¨œÅœáŒøœÖŒΩ boundaries
    const isNotClickable =
      /^\d+$/.test(part) || // ŒúœåŒΩŒø Œ±œÅŒπŒ∏ŒºŒøŒØ
      /^\d{3,5}(-\d{4})?$/.test(part) || // Postal codes
      part.length <= 2; // Œ†ŒøŒªœç ŒºŒπŒ∫œÅŒ¨ strings

    additionalComponents.push({
      id: `additional-${index++}`,
      label: part,
      type: 'custom',
      clickable: !isNotClickable, // ŒåŒªŒ± clickable ŒµŒ∫œÑœåœÇ Œ±œÄœå œÑŒπœÇ ŒµŒæŒ±ŒπœÅŒ≠œÉŒµŒπœÇ
      value: part,
      className: 'address-component'
    });
  }

  return additionalComponents;
}

/**
 * Combine parsed address components ŒºŒµ additional components Œ±œÄœå œÑŒø displayName
 */
export function parseFullAddress(result: GeocodeResult): AddressComponent[] {
  const baseComponents = parseGeocodeToComponents(result);
  const additionalComponents = parseDisplayNameToAdditionalComponents(result, baseComponents);

  // Combine Œ∫Œ±Œπ sort - Œ≤Œ¨Œ∂ŒøœÖŒºŒµ œÑŒ± administrative components œÄœÅœéœÑŒ±
  const allComponents = [...baseComponents, ...additionalComponents];

  return allComponents.sort((a, b) => {
    // Administrative components œÄœÅœéœÑŒ±
    if (a.clickable && !b.clickable) return -1;
    if (!a.clickable && b.clickable) return 1;

    // ŒúŒµœÑŒ¨ sort by type priority
    const typePriority = {
      'street': 1,
      'houseNumber': 2,
      'postalCode': 3,
      'city': 4,
      'region': 5,
      'custom': 6,
      'country': 7
    };

    return (typePriority[a.type] || 999) - (typePriority[b.type] || 999);
  });
}